import { Component, Input } from '@angular/core';
import { News } from '../../api/dashboard-api.model';
import { EmotionalEnum } from '../emotional-emoji/emotional.enum';

@Component({
    selector: 'news-card-component',
    templateUrl: './news-card.component.html'
})
export class NewsCardComponent {
    @Input()
    public newsItem: News;

    // 根据ODS的值获取相应的Logo图片
    public getImagenFrom(sdg: number): string {
        return `../../../images/sdg-images/sdg-${sdg}.png`;
    }

    // 根据新闻的情绪来决定卡片右侧的颜色
    public getRightBorderForTheNewsCard(emotion: number): { [key: string]: boolean } {
        return {
            'callout--green': emotion === EmotionalEnum.HAPPY,
            'callout--red': emotion === EmotionalEnum.SAD
        };
    }
}
